.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

ul {
  padding: 0;
}
li {
  list-style-type: none;
}
small {
  display: block;
}

button {
  padding: 1em 0;
  width: 125px;
  /* display: inline; */
  margin: 0.5em;
  border: none;
  background-color: #61dafb;
  color: white;
  text-transform: uppercase;
  cursor: pointer;
}
